

























export default m =
  name: 'navi'
  computed:
    active: ->
      array = ['', '', '', '', '', '']
      switch @$route.path
        when "/"
          array[0] = 'highlighted'
        when "/Brownian-Motion"
          array[1] = 'highlighted'
        when "/Chat-Noir"
          array[2] = 'highlighted'
        when "/Mine-Sweeper"
          array[3] = 'highlighted'
        when "/Minimum-Spanning-Tree"
          array[4] = 'highlighted'
        when "/Pinball"
          array[5] = 'highlighted'
      array



