






import _ from 'lodash'
#import Utils from '../modules/utils.coffee'

export default m =
  name: 'brownian-motion'
  data: ->
    width: '300'
    height: '150'

  mounted: ->
    window.onresize = @resize

  methods:
    init: ->

    resize: _.throttle ((e) ->
      console.log 'resize'
      @width = Math.max window.innerWidth * @scale, @px
      @height = Math.max window.innerHeight * @scale, @py
    ), 500






